import React, { useEffect } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const CONTAINER = styled.div`
  position: "fixed",
  zIdex: "1",
  top: "0",
  left: "0",
  height: "100%",
  width: "100%",
  backgroundColor: "rgba(0, 0, 0, 0.75)"
  ${(props) =>
    props.customStyle
  }
`

const CONTENT = styled.div`
  position: "relative",
  display: "flex",
  flexDirection: "column",
  rowGap: "20px",
  margin: "200px auto",
  maxWidth: "500px",
  background: "#fff",
  padding: "15px",
  alignItems: "center",
  borderRadius: "5px"
  ${(props) =>
    props.customStyle
  }
`

const BUTTON = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
  color: #FFF;
  width: 100px;
  padding: 5px;
  border-radius: 5px;
  ${(props) =>
    props.customStyle
  }
`

const BUTTONX = styled.button`
  position: "absolute",
  top: "-15px",
  right: "-15px",
  fontSize: "1rem",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  cursor: "pointer",
  fontWeight: "bold",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  background: "#000",
  color: "#FFF",
  border: "none",
  width: "30px",
  height: "30px",
  borderRadius: "300px"
  ${(props) =>
    props.customStyle
  }
`

function Modal ({open, escape, outside, modalContent, close, showClose, buttonContent, styleContainer, styleContent, styleButton, styleX}) {
  if (!open) {
    return null
  }

  const styleDefault = {
    button : {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      background: "#000",
      color: "#FFF",
      width: "100px",
      padding: "5px",
      borderRadius: "5px"
    },

    buttonX : {
      position: "absolute",
      top: "-15px",
      right: "-15px",
      fontSize: "1rem",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      cursor: "pointer",
      fontWeight: "bold",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      background: "#000",
      color: "#FFF",
      border: "none",
      width: "30px",
      height: "30px",
      borderRadius: "300px"
    }
  }

  useEffect(() => {
    if (open && escape) {
      window.addEventListener("keydown", (e) => {
        if (e.key === "Escape") {
          close()
          window.removeEventListener("keydown", close())
        }
      })
    }
  }, [open])

  const outsideModal = (e) => {
    outside && e.target.id === "container" && close()
  }

  return (
    <CONTAINER
      id = "container"
      onClick = {(e) => outsideModal(e)}
      style = {style && style.container ? style.container : styleDefault.container}
    >
      <CONTENT
        id = "content"
        style = {style && style.content ? style.content : styleDefault.content}
      >
        {modalContent}
        <BUTTON
          onClick = {close}
          customStyle = {styleButton}
        >
          {buttonContent}
        </BUTTON>
        {showClose && <BUTTONX onClick = {close} style = {styleDefault.buttonX}>x</BUTTONX>}
      </CONTENT>
    </CONTAINER>
  )
}

Modal.propTypes = {
  open: PropTypes.bool,
  escape: PropTypes.bool,
  outside: PropTypes.bool,
  showClose: PropTypes.bool,
  modalContent: PropTypes.string,
  buttonContent: PropTypes.string,
  close: PropTypes.func.isRequired,
  style: PropTypes.object
}

Modal.defaultProps = {
  open: false,
  escape: true,
  outside: true,
  showClose: false,
  modalContent: "React modal is open!",
  buttonContent: "Close"
}

export default Modal;

